import { Fragment } from 'react';
import { graphql, Link } from 'gatsby';
import { css } from '@emotion/react';
import { kebabCase } from 'lodash-es';

import Footer from '@/components/Footer.tsx';
import SiteNav from '@/components/header/SiteNav.tsx';
import Wrapper from '@/components/Wrapper.tsx';
import IndexLayout from '@/layouts/index.tsx';
import { PostFullContent, PostFullHeader, PostFullTitle } from '@/styles/shared.ts';
import { inner, outer, SiteMain } from '@/styles/shared.ts';
import { NoImage, PostFull } from '@/templates/post.tsx';

const PageTemplate = css`
  .site-main {
    margin-top: 64px;
    padding-bottom: 4vw;
  }
`;

const TagArea = css`
  ul {
    padding-left: initial;

    li {
      transform: translateY(0);
      display: inline-block;
      margin-right: 12px;
      padding: 2px 4px;
      color: var(--main-color);
      list-style: none;
      background: var(--tag-background);
      border: 1px solid var(--image-border-color);
      border-radius: 6px;
      transition: all 0.4s ease;

      &:hover {
        transform: translateY(-3px);
        box-shadow: 0 8px 20px var(--tag-boxshadow);
      }

      a {
        padding: 6px;
        color: var(--main-color);
        text-decoration: none;
        box-shadow: none;

        &:hover {
          box-shadow: none;
        }
      }
    }
  }
`;

type Props = {
  data: {
    allMarkdownRemark: {
      group: Array<{
        fieldValue: string;
        totalCount: number | string;
      }>;
    };
  };
};

export const Head = () => (
  <Fragment>
    <title>Tags</title>
    <meta name="robots" content="noindex" />
  </Fragment>
);

export default function Tags({ data }: Props) {
  return (
    <IndexLayout>
      <Wrapper css={PageTemplate}>
        <SiteNav />
        <main id="site-main" className="site-main" css={[SiteMain, outer]}>
          <div css={inner}>
            <article className="post page" css={[PostFull, NoImage]}>
              <PostFullHeader className="post-full-header">
                <PostFullTitle className="post-full-title">Tags</PostFullTitle>
              </PostFullHeader>

              <PostFullContent className="post-full-content">
                <div className="post-content">
                  <div css={TagArea}>
                    <ul>
                      {data.allMarkdownRemark.group.map((tag) => (
                        <li key={tag.fieldValue}>
                          <Link to={`/tags/${kebabCase(tag.fieldValue)}/`}>
                            {tag.fieldValue} ({tag.totalCount})
                          </Link>
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
              </PostFullContent>
            </article>
          </div>
        </main>
        <Footer />
      </Wrapper>
    </IndexLayout>
  );
}

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: { frontmatter: { draft: { ne: true } } }
      sort: { frontmatter: { date: DESC } }
      limit: 2000
    ) {
      group(field: { frontmatter: { tags: SELECT } }) {
        fieldValue
        totalCount
      }
    }
  }
`;
